<template>
    <div class="Rating-gray">
        <i v-for="(item,index) in itemClasses" :key="index" class="fa" :class="item"></i>
    </div>
</template>

<script>
import {computed, ref} from '@vue/composition-api'
    export default {
        name: 'Rating',
        props: {
            rating:Number
        },
        setup(props, {root}){
            //星期的长度
            const LENGTH =ref(5)
            //星星对应的class
            const CLS_ON =ref('fa-star')
            const CLS_HALF =ref('fa-star-half-empty')
            const CLS_OFF =ref('fa-star-o')
            const itemClasses =computed(() => {
                //4.8四个全星 1个半星
                let result=[];

                //对分数进行处理，向下取0.5的倍数
                let score =Math.floor(props.rating*2)/2;
                //控制半星
                let hasDecimal=score%1!==0;
                //全星
                let integer=Math.floor(score);

                //全星放入到数组中
                for(let i=0;i<integer;i++){
                    result.push(CLS_ON.value)
                }

                //半星
                if(hasDecimal){
                    result.push(CLS_HALF.value);
                }

                //补齐
                while(result.length<LENGTH.value){
                    result.push(CLS_OFF.value);
                }

                return result
            })

            return {
                CLS_ON,
                CLS_HALF,
                CLS_OFF,
                itemClasses
            }
        }
    }
</script>

<style scoped>
    .Rating-gray {
        margin-right: 1.06667vw;
        color:#ffbe00;
        display: inline-block;
    }
</style>